.Field {
  height: 100%;
  width: 100%;
  min-height: 20px;
  border: 1px solid var(--border-color);
  color: var(--text-color);
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 3px;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  opacity: 0.8;
}

.Elevated {
  background-color: var(--field-color);  
}
.Status {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 30%;
}

.Field-2048 {
  background-color: var(--field-color-won);
}
.Field-1024, .Field-512, .Field-256, .Field-128 {
  background-color: var(--field-color-almost-won);
}
.Field-64, .Field-32 {
  background-color: var(--field-color-move);
}



